"use client"

import { useState, useEffect } from "react"
import { useRouter, useSearchParams } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { BackButton } from "@/components/ui/BackButton"
import { useToast } from "@/components/ui/use-toast"
import { Loader2, AlertCircle } from "lucide-react"
import { Alert, AlertDescription } from "@/components/ui/alert"
import apiClient from "@/services/apiClient"
import { useAuth } from "@/contexts/AuthContext"

interface Usuario {
  id: number
  nome: string
  username: string
  email: string
  cargo: string
  perfil: string
}

export default function GerenciarPerfilUsuarioSimplificado() {
  const router = useRouter()
  const searchParams = useSearchParams()
  const { toast } = useToast()
  const { refreshAuth, isAuthenticated } = useAuth()

  const userId = searchParams.get("id") ? Number.parseInt(searchParams.get("id") as string, 10) : 0

  const [usuario, setUsuario] = useState<Usuario | null>(null)
  const [carregando, setCarregando] = useState(true)
  const [perfilSelecionado, setPerfilSelecionado] = useState<string>("")
  const [salvando, setSalvando] = useState(false)
  const [erro, setErro] = useState<string | null>(null)

  // Controle de tentativas para evitar loops infinitos
  const [tentativas, setTentativas] = useState(0)
  const MAX_TENTATIVAS = 3

  // Carregar usuário
  useEffect(() => {
    const carregarUsuario = async () => {
      if (!userId || isNaN(userId)) {
        setErro("ID de usuário inválido")
        setCarregando(false)
        return
      }

      // Limitar o número de tentativas para evitar loops
      if (tentativas >= MAX_TENTATIVAS) {
        setErro(`Número máximo de tentativas excedido (${MAX_TENTATIVAS}). Recarregue a página.`)
        setCarregando(false)
        return
      }

      try {
        setCarregando(true)
        setErro(null)

        // Incrementar contador de tentativas
        setTentativas((prev) => prev + 1)
        console.log(`Tentativa ${tentativas + 1} de ${MAX_TENTATIVAS} para carregar usuário ${userId}`)

        // Tentar renovar o token apenas na primeira tentativa
        if (tentativas === 0) {
          try {
            await refreshAuth()
          } catch (error) {
            console.error("Erro ao renovar token:", error)
          }
        }

        console.log("Buscando usuário com ID:", userId)
        const response = await apiClient.get(`/usuarios/${userId}`)
        const data = response.data
        console.log("Dados do usuário recebidos:", data)

        setUsuario(data)
        setPerfilSelecionado(data.perfil || "")
      } catch (error: any) {
        console.error("Erro ao carregar usuário:", error)

        // Verificar se é um erro de autenticação
        if (error.response && error.response.status === 401) {
          setErro("Sua sessão expirou. Por favor, faça login novamente.")
          setTimeout(() => router.push("/login"), 2000)
          return
        }

        setErro(`Não foi possível carregar os dados do usuário. ${error.message || ""}`)
      } finally {
        setCarregando(false)
      }
    }

    // Só executar se tivermos um ID e ainda não tivermos carregado o usuário
    if (userId && !usuario && carregando) {
      carregarUsuario()
    }
  }, [userId, usuario, carregando, tentativas, router, refreshAuth])

  // Verificar autenticação
  useEffect(() => {
    if (!isAuthenticated && !carregando && tentativas > 0) {
      router.push("/login")
    }
  }, [isAuthenticated, carregando, router, tentativas])

  const handleSalvarPerfil = async () => {
    if (!usuario) return

    try {
      setSalvando(true)
      setErro(null)

      if (!perfilSelecionado) {
        toast({
          title: "Atenção",
          description: "Selecione um perfil para o usuário.",
          variant: "destructive",
        })
        return
      }

      // Tentar renovar o token antes de fazer a requisição
      try {
        await refreshAuth()
      } catch (error) {
        console.error("Erro ao renovar token antes de salvar:", error)
      }

      console.log("Atribuindo perfil:", perfilSelecionado, "ao usuário:", usuario.id)

      // Abordagem direta: atualizar o usuário com o novo perfil
      const usuarioAtualizado = {
        ...usuario,
        perfil: perfilSelecionado,
      }

      // Remover campos sensíveis ou que possam causar problemas
      delete usuarioAtualizado.permissoes

      // Enviar a atualização diretamente
      await apiClient.put(`/usuarios/${usuario.id}`, usuarioAtualizado, {
        headers: {
          "Content-Type": "application/json",
        },
      })

      console.log("Perfil atribuído com sucesso")

      toast({
        title: "Sucesso",
        description: "Perfil atualizado com sucesso.",
      })

      // Voltar para a lista de usuários após salvar com sucesso
      setTimeout(() => {
        router.push("/usuarios")
      }, 1500)
    } catch (error: any) {
      console.error("Erro ao salvar perfil:", error)
      console.error("Detalhes da resposta:", error.response?.data)

      // Verificar se é um erro de autenticação
      if (error.response && error.response.status === 401) {
        setErro("Sua sessão expirou. Por favor, faça login novamente.")
        setTimeout(() => router.push("/login"), 2000)
        return
      }

      toast({
        title: "Erro",
        description: `Não foi possível salvar o perfil do usuário. ${error.message || ""}`,
        variant: "destructive",
      })
    } finally {
      setSalvando(false)
    }
  }

  if (carregando) {
    return (
      <div className="flex justify-center items-center h-screen">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
        <p className="ml-2">Carregando dados do usuário...</p>
      </div>
    )
  }

  if (erro) {
    return (
      <div className="container mx-auto py-6">
        <div className="flex items-center mb-6">
          <BackButton />
          <h1 className="text-2xl font-bold ml-4">Erro</h1>
        </div>
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>{erro}</AlertDescription>
        </Alert>
        <div className="mt-4">
          <Button onClick={() => router.push("/usuarios")}>Voltar para Lista de Usuários</Button>
        </div>
      </div>
    )
  }

  if (!usuario) {
    return (
      <div className="container mx-auto py-6">
        <div className="flex items-center mb-6">
          <BackButton />
          <h1 className="text-2xl font-bold ml-4">Usuário não encontrado</h1>
        </div>
        <p>O usuário solicitado não foi encontrado.</p>
        <div className="mt-4">
          <Button onClick={() => router.push("/usuarios")}>Voltar para Lista de Usuários</Button>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto py-6">
      <div className="flex items-center mb-6">
        <BackButton />
        <h1 className="text-2xl font-bold ml-4">Gerenciar Perfil do Usuário (Simplificado)</h1>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>{usuario.nome}</CardTitle>
          <p className="text-sm text-gray-500">
            {usuario.username} | {usuario.email} | {usuario.cargo}
          </p>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div>
              <p className="text-sm font-medium mb-2">
                Perfil atual: <span className="font-bold">{usuario.perfil || "Não definido"}</span>
              </p>
              <Select value={perfilSelecionado} onValueChange={setPerfilSelecionado}>
                <SelectTrigger className="w-full md:w-[300px]">
                  <SelectValue placeholder="Selecione um perfil" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="ADMINISTRADOR">Administrador</SelectItem>
                  <SelectItem value="GERENTE">Gerente</SelectItem>
                  <SelectItem value="VENDEDOR">Vendedor</SelectItem>
                  <SelectItem value="OPERADOR">Operador</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div>
              <p className="text-xs text-gray-500 mt-2">
                Ao salvar o perfil, as permissões padrão serão atribuídas automaticamente pelo sistema.
              </p>
              <p className="text-xs text-gray-500">
                Esta é uma versão simplificada que atualiza apenas o perfil do usuário.
              </p>
            </div>

            <div className="flex gap-2 pt-4">
              <Button onClick={handleSalvarPerfil} disabled={salvando}>
                {salvando ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                {salvando ? "Salvando..." : "Salvar Perfil"}
              </Button>
              <Button variant="outline" onClick={() => router.push("/usuarios")}>
                Voltar para Lista
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
